import React from 'react';

import {
    Grid,
    Typography,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    }
}));

export const Community = props => {
    const classes = useStyles();

    return (
        <>
            <Typography color="secondary" variant="h3">Our Community</Typography>
                <Grid xs={12}><Divider className={classes.divider} /></Grid>
            <Grid 
                item
                xs={12}
            >
                <Typography variant="h5">How to donate to open source development</Typography>
                <Typography variant="subtitle1">Please donate to the work of the developers who provide our upstream software systems. These include Dia, Python, Linux, Git, Django.</Typography>
                <br />
                <br />
                <Grid xs={12}><Divider className={classes.divider} /></Grid>
                <Typography variant="h5">How to get involved</Typography>
                <Typography variant="subtitle1">We have a thriving online community at our Matrix server at chat.laberation.net. You can also connect via Telegram at @Laberation and via IRC.</Typography>
                <ul>
                    <li><Typography variant="subtitle1">Speak to us and let us know you want in</Typography></li>
                    <li><Typography variant="subtitle1">Join our Gitlab server</Typography></li>
                    <li><Typography variant="subtitle1">Make a branch or fork of our systems and develop your contributions. Upload your documentation, changes and new ideas and let us know what you did.</Typography></li>
                </ul>
            </Grid>
        </>
    );
}
