import React from 'react';

import {
    Grid,
    Typography,
    Button,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import Paragraph from '../../GenericCompoents/Paragraph';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    }
}));

export const Laberation = props => {
    const { redirectTo } = props;

    const classes = useStyles();

    return (
        <>
            <Typography variant="h3" color="secondary" className={classes.title}>Laberation</Typography>
            <Paragraph
                title=""
                text={[
                    "Imagine a lab that cures diseases but doesn’t need a degree, lab coats, or goggles. Imagine a lab that changes the very paradigm of healthcare as we know it. Imagine a lab that you can use as easily as an online store.",
                    "That's Laberation.",
                ]}
            >
                <ul>
                    <li><Typography variant="subtitle1">Share the molecure-vector that carries the gene that you adjusted to get rid of the error that made you susceptible to cancer.</Typography></li>
                    <li><Typography variant="subtitle1">Buy the edit set that you can personalise to add a new multi-coloured hair pattern that swirls down your back like a self-grown tattoo, expressing your creativity on the unique canvas that is you.</Typography></li>
                    <li><Typography variant="subtitle1">Explore a blockchain based, distributed peer to peer bio-database made by the people for the people, decentralised and disintermediated.</Typography></li>
                    <li><Typography variant="subtitle1">Run a server and set your price For providing support on your node.</Typography></li>
                    <li><Typography variant="subtitle1">Buy support packages through Laberation itself.</Typography></li>
                    <li><Typography variant="subtitle1">Explore Freedom of Form: an evolution revolution. </Typography></li>
                    <li><Typography variant="subtitle1">Out-evolve disease and outlive aging.</Typography></li>
                    <li><Typography variant="subtitle1">Adapt to harsh environments in weeks or months.</Typography></li>
                </ul>
            </Paragraph>
            <Paragraph
                surpressDivider={true}
                text={[
                    "Want to thrive in space? Want to grow a new face? Take a GUESS and Geneticise your dream body.",
                    "Organs are the new apps. Laberation is the app store. The GUESS is the IDE for DNA connected to that store.",
                    "This is where you plot out the story of your own biological future. This is where you write yourself.",
                    "Laberation from aberration through collaboration in Laberation! Courtesy of Vulpine Designs."
                ]}
            />
            <Grid
                item 
                xs={12}
            >
                <Divider className={classes.divider} />
            </Grid>
            <Grid
                item
                container
                xs={12}
                justify="center"
            >
                <Button variant="outlined" onClick={() => redirectTo('project')}>Back to projects</Button>
            </Grid>
        </>
    );
};
