import React from 'react';

import {
    Grid,
    Typography,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import Error from './Error';

import { GuessPage } from './Guess/Guess';
import { SabatomiRap } from './SabatomiRap/SabatomiRap';
import { Laberation } from './Laberation/Laberation';

import CardLink from '../GenericCompoents/CardLink';

const useStyles = makeStyles(theme => ({
    container: {
        marginBottom: theme.spacing(3)
    },
    divider: {
        marginTop: 5,
        marginBottom: 5,
    },
    button: {
        margin: 4
    },
    card: {
        margin: theme.spacing(2)
    }
}));

const Projects = props => {
    const classes = useStyles();

    const {
        postId,
        redirectTo
    } = props;

    const projectsDict = {
        "guess": (<GuessPage redirectTo={redirectTo} />),
        "sabatomirap": (<SabatomiRap redirectTo={redirectTo} />),
        "laberation": (<Laberation redirectTo={redirectTo} />)
    };

    const page = projectsDict[postId] ? projectsDict[postId] : <Error />;

    return postId ? (
        <>
            {page}
        </>
    ) : (
        <>
            <Typography
                variant="h3"
                color="secondary"
                className={classes.title}
            >
                Our Projects
            </Typography>
            <Grid item xs={12}>
                <Divider className={classes.divider} />
            </Grid>
            <Grid
                container
                justify="center"
                spacing={12}
            >
                <CardLink
                    largeItem
                    classname={classes.card}
                    title="Guess"
                    preview="The GUESS Unified Editing Suite and SDK: An IDE (Integrated Development Environment) for DNA."
                    imgSrc={`/images/guess-card-logo.png`}
                    onClick={() => redirectTo('project/guess')}
                />
                <CardLink
                    largeItem
                    classname={classes.card}
                    title="Laberation"
                    preview="Laberation: Many hands make gene editing light work. Use Laberation to buy, sell and share collections of edits to fix problems and enhance lives!"
                    imgSrc="/images/Abstract/lab-image.jpg"
                    onClick={() => redirectTo('project/laberation')}
                />
                <CardLink
                    largeItem
                    classname={classes.card}
                    title="Sabatomirap"
                    preview="SabatomiRap: A metal 3D printer and scanning electron microscope project, forked from MetalicaRap (Labitat, Copenhagen; RepRap wiki) and a similar project (HackADay)."
                    imgSrc="/images/Abstract/3d-printer.jpg"
                    onClick={() => redirectTo('project/sabatomirap')}
                />
            </Grid>
        </>
    );
}


export default Projects;
