import React, { forwardRef } from 'react';

import {
    Divider,
    Grid, 
    makeStyles,
    Typography, 
} from '@material-ui/core'; 

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    }
}));

export const Paragraph = forwardRef((props, ref) => {
    const classes = useStyles();

    const {
        title,
        text,
        id,
        children,
        surpressDivider = false 
    } = props;

    const textProps = text.map(val => 
        <>
            <Typography variant="subtitle1" key={`paragraph-text-${val}`}>{val}</Typography>
            <br />
        </>
    );

    return (
        <Grid
            item 
            container
            xs={12}
            justify="flex-start"
            direction="column"
        >
            {!surpressDivider ?
                <Grid item >
                    <Divider className={classes.divider} />
                </Grid> : null
            }
            <Grid justify="center">
                <Typography 
                    variant="h3" 
                    color="secondary" 
                    className={classes.title}
                    id={id}
                    ref={ref}
                >
                    {title}
                </Typography>
            </Grid>
            {textProps}
            {children}
        </Grid>
    );
});

export default Paragraph;
