import React, { useState } from 'react';

import {
    AppBar,
    Toolbar,
    Typography,
    makeStyles,
    IconButton,
    Button, 
    Hidden, 
    Menu,
} from '@material-ui/core';

import clsx from 'clsx';

import { useMediaQuery } from '@material-ui/core';

import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import YouTubeIcon from '@material-ui/icons/YouTube';
import Brightness2Icon from '@material-ui/icons/Brightness2';
import Brightness5Icon from '@material-ui/icons/Brightness5';

import { AiFillGitlab } from 'react-icons/ai';

import ExpandLess from '@material-ui/icons/ExpandLess';
import ExpandMore from '@material-ui/icons/ExpandMore';

const profileLinks = (
    <div>
        <IconButton aria-label="Twitter" href="https://twitter.com/vulpinedesigns">
            <TwitterIcon />
        </IconButton>
        <IconButton aria-label="YouTube" href="https://www.youtube.com/channel/UCN0TZ3evdMxD0J4WOQjPsLQ">
            <YouTubeIcon />
        </IconButton>
        <IconButton aria-label="Telegram" href="https://www.linkedin.com/company/vulpine-designs-ltd">
            <LinkedInIcon />
        </IconButton>
        <IconButton aria-label="GitLab" href="https://gitlab.vulpinedesigns.com/">
            <AiFillGitlab />
        </IconButton>
    </div>
);

const useStyles = makeStyles(theme => ({
        menuButton: {
            borderRadius: 0,
        },
        title: {
            marginRight: 'auto',
            padding: 0,
            textTransform: 'none',
            height: theme.mixins.toolbar['@media (min-width:600px)'].minHeight,
            borderRadius: 0
        },
        appBar: {
            zIndex: theme.zIndex.drawer + 1, 
        },
        defaultHeight: {
            ehight: theme.mixins.toolbar.minHeight
        },
        width600: {
            height: theme.mixins.toolbar['@media (min-width:600px)'].minHeight
        },
        width0Landscape: {
            height: theme.mixins.toolbar['@media (min-width:0px) and (orientation: landscape)'].minHeight
        },
        toggleDarkMode: {
            borderRadius: 0,
            backgroundColor: theme.palette.background.paper,
            marginRight: theme.spacing(4)
        }
    })
); 

const Header = props => {
    const [currentMonth, currentDay] = [new Date().getMonth(), new Date().getDate()];
    let title = "";
    if(currentMonth === 7 && currentDay === 13) title = "Lupine";
    else if(currentMonth === 4 && currentDay === 27) title = "Hyeen";
    else title = "Vulpine";

    const classes = useStyles();
    const [linksMenu, setLinksMenu] = useState(null);

    const { 
        redirectTo, 
        setDarkmode, 
        darkMode
    } = props;

    const openLinks = (event) =>
        setLinksMenu(event.currentTarget);

    const closeLinks = () => 
        setLinksMenu(null);

    // The height of the header/footer changes depending on the screen size/orientation
    //  as defined in the theme, we need to account for that in the min size for the main content  
    const minWidth0Landscape = useMediaQuery('@media (min-width:0px) and (orientation: landscape)');
    const minWidth600 = useMediaQuery('@media (min-width:600px)');

    let toolBarHeight = '';
    if(minWidth0Landscape && !minWidth600)
        toolBarHeight = classes.width0Landscape;
    else if(minWidth600)
        toolBarHeight = classes.width600;
    else 
        toolBarHeight = classes.defaultHeight;

    return (
        <AppBar position="sticky" className={classes.appBar}>
            <Toolbar>
                <Hidden smDown>
                    <Button 
                        className={clsx(classes.menuButton, toolBarHeight)}
                        onClick={() => props.toggleDraw('top')}
                        size="large"
                    >
                        {props.drawOpen.open ? 
                            <ExpandLess fontSize="large" /> : <ExpandMore fontSize="large" />
                        }
                    </Button>
                </Hidden>
                <Button 
                    className={clsx(classes.title, toolBarHeight)}
                    onClick={() => redirectTo('home')}
                >
                    <img 
                        alt="Vulpine Designs home"
                        src="./images/logo.png"
                        height={57}
                    />
                    <Typography variant="h6">
                        {title} Designs 
                    </Typography>
                </Button>
                <Hidden smDown>
                    <Button 
                        aria-label="Twitter" 
                        onClick={event => setDarkmode(!darkMode)}
                        color={darkMode ? "secondary" :  ""}
                        tool
                        className={clsx(classes.toggleDarkMode, toolBarHeight)}
                    >
                        {darkMode ? <Brightness2Icon /> : <Brightness5Icon color="secondary" />}
                    </Button>
                </Hidden>
                <Hidden xsDown>
                    {profileLinks}
                </Hidden>
                <Hidden smUp>
                    <Button 
                        onClick={openLinks}
                        endIcon={<ExpandMore />}
                        className={classes.linksButton}
                        size="small"
                    >
                        Links 
                    </Button>
                    <Menu
                        anchorEl={linksMenu}
                        open={Boolean(linksMenu)}
                        onClose={closeLinks}
                    >
                        {profileLinks}
                    </Menu>
                </Hidden>
            </Toolbar>
        </AppBar>
    );
}

export default Header;
