import React from 'react';

import {
    Grid,
    Typography,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    }
}));

export const Plans = props => {
    const classes = useStyles();

    return (
        <>
            <Typography className={classes.title} color="secondary" variant="h3">Our Plans</Typography>
            <Grid xs={12}><Divider className={classes.divider} /></Grid>
            <Grid 
                item
                container
                direction="column"
            >
                <Typography variant="h5">Without going into too much detail, let's make it clear that we have ideas for what we are doing that extend many decades into the future. Each project is a stepping stone towards the next.</Typography>
                <ul>
                    <li><Typography variant="subtitle1">SabatomiRap allows metal 3D printing and electron microscopy at a much lower price point. This will facilitate our lab.</Typography></li>
                    <li><Typography variant="subtitle1">The Geneticiser and the GUESS allow the molecular bioprinting and simulation of gene edits. These will facilitate:</Typography></li>
                    <li><Typography variant="subtitle1">The biologification of electronics (Biotronics). This is the incorporation of gadgets and devices into novel organs encoded in our DNA. This will facilitate telepathy of sorts (biologified comms devices), and...</Typography></li>
                    <li><Typography variant="subtitle1">Shapeshifting, caused when we biologify the Geneticiser and GUESS themselves. This will facilitate:</Typography></li>
                    <li><Typography variant="subtitle1">Enhanced adaptation to extreme environments including temperatures, radiation and chemicals. This will facilitate space exploration and colonisation of new celestial bodies.</Typography></li>
                </ul>
                <Typography variant="h5">We will start by:</Typography>
                <ul>
                    <li><Typography variant="subtitle1">Coding the 25 modules of the GUESS, utilising AI and machine learning and even quantum computing where needed.</Typography></li>
                    <li><Typography variant="subtitle1">Building prototypes and iterate the design of the Sabatomirap and the Geneticiser.</Typography></li>
                </ul>
            </Grid>
        </>
    );
};
