import React from 'react';

import {
    Grid,
    Typography,
    Divider,
    Link
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import { DisplayItem } from '../../GenericCompoents/DisplayItem';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    },
    title: {
        fontSize: '2.7em'
    },
    body: {
        fontSize: '1.4em'
    },
    conatiner: {
        marginTop: 30,
        marginBottom: 20
    },
    header: {
        backgroundColor: theme.palette.secondary[theme.palette.type],
        paddingLeft: theme.spacing(1),
        opacity: 0.7
    }
}));

export const Events = props => {
    const classes = useStyles(); 

    return (
        <>
            <Typography
                variant="h3"
                color="secondary"
                className={classes.title}
            >
                Events You Can Find Us
            </Typography>
            <Grid
                container
                justify="center"
                spacing={12}
            >
                <Grid item xs={12}>
                    <Grid item xs={12}>
                        <Divider className={classes.divider} />
                    </Grid>
                    <Grid item xs={12} className={classes.header}>
                        <Typography className={classes.body} variant="h5">25-29th October</Typography>
                    </Grid>
                    <DisplayItem
                        imageUrl="/images/Abstract/IAF.jpg"
                    >
                        <Grid 
                            container
                            className={classes.conatiner}
                        >
                            <Grid item lg={1} xs={false} />
                            <Grid item lg={10} xs={12}>
                                <Typography className={classes.title} variant="h2">
                                    International Astronautical Congress 2021
                                </Typography>
                                <Typography className={classes.body} variant="h5">
                                    We were there. Dubai Convention Centre (in collaboration with <Link href="https://nexusaurora.org/">Nexus Aurora</Link> and <Link href="https://www.hubtropolis.com/">HubTropolis</Link>)
                                </Typography>
                                <Typography className={classes.body} variant="h5" color="secondary">
                                    <Grid container spacing={2} justify="flex-start">
                                        <Grid item><Link href="https://iac2021.org/">IAC 2021 </Link></Grid>
                                        <Grid item><Link href="https://www.iafastro.org/events/iac/iac-2021/">IAF Events</Link></Grid>
                                    </Grid>
                                </Typography>
                            </Grid>
                            <Grid item lg={1} xs={false} />
                        </Grid>
                    </DisplayItem>
                </Grid>
                <Grid item xs={12}>
                    <Grid item xs={12}>
                        <Divider className={classes.divider} />
                    </Grid>
                    <Grid item xs={12} className={classes.header}>
                        <Typography className={classes.body} variant="h5">1st November</Typography>
                    </Grid>
                    <DisplayItem
                        imageUrl="/images/Abstract/burjKhalifa.webp"
                    >
                        <Grid 
                            container
                            className={classes.conatiner}
                        >
                            <Grid item lg={1} xs={false} />
                            <Grid item lg={10} xs={12}>
                                <Typography className={classes.title} variant="h2">
                                    HubTropolis High³
                                </Typography>
                                <Typography className={classes.body} variant="h5">
                                    We were there. The Burj Khalifa, Dubai, 1st November 2021.
                                </Typography>
                                <Typography className={classes.body} variant="h5" color="secondary">
                                    <Grid container spacing={2} justify="flex-start">
                                        <Grid item><Link href="https://www.youtube.com/watch?v=ADX1Fffc-5E">YouTube Invite</Link></Grid>
                                        <Grid item><Link href="https://hubtropolis.com/">Hubtropolis</Link></Grid>
                                    </Grid>
                                </Typography>
                            </Grid>
                            <Grid item lg={1} xs={false} />
                        </Grid>
                    </DisplayItem>
                </Grid>
            </Grid>
        </>
    );
}
