import React, { useState } from 'react';

import {
    Typography,
    Grid,
    Button,
    TextField,
    Backdrop,
    CircularProgress,
    Card,
    Link
} from '@material-ui/core';

import {
    makeStyles,
    useTheme
} from '@material-ui/core/styles';

import ReCAPTCHA from 'react-google-recaptcha';

import emailjs from 'emailjs-com';

import Paragraph from '../../GenericCompoents/Paragraph';

const useStyles = makeStyles(theme => ({
    textFieldHolder: {
        paddingTop: theme.spacing(1),
        paddingRight: theme.spacing(1),
        paddingLeft: theme.spacing(1)
    },
    text: {
        width: "100%"
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff'
    },
    sendEmail: {
        paddingBottom: theme.spacing(1),
        paddingTop: theme.spacing(1)
    }
}))

export const Contact = props => { 
    const classes = useStyles();
    const theme = useTheme();

    const [gRecaptchaResponse, setGRecaptchaResponse] = useState('');
    const [emailAddr, setEmailAddr] = useState();
    const [emailName, setEmailName] = useState();
    const [emailCont, setEmailCont] = useState();
    const [loading, setLoading] = useState(false);

    const sendEmail = async () => {
        const params = {
            name: emailName, 
            email: emailAddr, 
            message: emailCont,
            'g-recaptcha-response': gRecaptchaResponse,
        };
        setLoading(true);
        emailjs.send('service_lm3xfmk', 'template_k5t8ps1', params, 'user_FTEYbAMX9QlUWZQLbsBfu')
            .then(res => {
                setEmailAddr('');
                setEmailName('');
                setEmailCont('');
                setGRecaptchaResponse('');
                setLoading(false);
            }).catch(e => {
                setEmailAddr('');
                setEmailName('');
                setEmailCont('');
                setLoading(false);
            });
    };

    return (
        <>
            <Backdrop className={classes.backdrop} open={loading}>
                <CircularProgress color="inherit" />
            </Backdrop>
            <Typography color="secondary" variant="h3" className={classes.title}>Contact Us</Typography>
            <Paragraph
                title=""
                text={[]}
            >
                <Grid
                    item
                    container
                    justify="center"
                    direction="row"
                >
                    <Grid
                        item
                        xs={12}
                        justify="center"
                    >
                        <Typography style={{ textAlign: "center" }} variant="subtitle1">On Social Media, you can find us on <Link href="https://twitter.com/vulpinedesigns">Twitter</Link>, <Link href="https://www.youtube.com/channel/UCN0TZ3evdMxD0J4WOQjPsLQ">Youtube</Link>, <Link href="https://www.linkedin.com/company/vulpine-designs-ltd">LinkedIn</Link>.</Typography>
                        <br />
                        <Typography style={{ textAlign: "center" }} variant="subtitle1">To apply for jobs, you need to first look at our <Link href="/#/community">community page</Link> and our <Link href="/#/team">team page</Link>.</Typography>
                        <br />
                        <Typography style={{ textAlign: "center" }} variant="subtitle1">To email us, you can use this form:</Typography>
                        <br />
                    </Grid>
                    <Grid item xs={false} sm={1} md={3} />
                    <Grid item xs={12} sm={10} md={6}>
                        <Card>
                            <Grid container>
                                <Grid 
                                    className={classes.textFieldHolder}
                                    item 
                                    xs={12}  
                                >
                                    <Typography variant="h5">Send Email</Typography>
                                </Grid>
                                <Grid 
                                    className={classes.textFieldHolder}
                                    item 
                                    xs={12} 
                                    sm={6} 
                                >
                                    <TextField 
                                        className={classes.text}
                                        label="Name"
                                        variant="outlined"
                                        name="user_name"
                                        value={emailName}
                                        onChange={e => setEmailName(e.target.value)}
                                    />
                                </Grid>
                                <Grid 
                                    className={classes.textFieldHolder}
                                    item 
                                    xs={12} 
                                    sm={6} 
                                >
                                    <TextField 
                                        className={classes.text}
                                        label="Email"
                                        variant="outlined"
                                        name="email"
                                        value={emailAddr}
                                        onChange={e => setEmailAddr(e.target.value)}
                                    />
                                </Grid>
                                <Grid item xs={false} md={3} />
                                <Grid item xs={false} md={3} />
                                <Grid 
                                    className={classes.textFieldHolder}
                                    item 
                                    xs={12} 
                                >
                                    <TextField 
                                        className={classes.text}
                                        multiline
                                        rows={6}
                                        label="Message"
                                        variant="outlined"
                                        value={emailCont}
                                        onChange={e => setEmailCont(e.target.value)}
                                    />
                                </Grid>
                                <Grid 
                                    className={classes.textFieldHolder}
                                    item 
                                    container
                                    xs={12} 
                                    justify="center"
                                >
                                    <ReCAPTCHA
                                        sitekey="6LevEOUcAAAAACwhbwU8N-QtcWiZLvJ7aRihGaHG"
                                        onChange={value => setGRecaptchaResponse(value)}
                                        theme={theme.palette.type}
                                    />
                                </Grid>
                                <Grid 
                                    item 
                                    container 
                                    xs={12} 
                                    justify="center"
                                    className={classes.sendEmail}
                                >
                                    <Button 
                                        color="secondary" 
                                        variant="outlined" 
                                        onClick={sendEmail}
                                        disabled={!gRecaptchaResponse}
                                    >
                                        Send Email
                                    </Button>
                                </Grid>
                            </Grid>
                        </Card>
                    </Grid>
                    <Grid item xs={false} sm={1} md={3} />
                </Grid>
            </Paragraph>
        </>
    );
};
