import React from 'react';

import {
    makeStyles
} from '@material-ui/core/styles';

const getStyles = imageUrl => makeStyles(theme => ({
    image: {
        width: '100%', 
        position: 'relative',
        opacity: 1,
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        '&::after': {
            content: '""',
            backgroundImage: `url("${imageUrl}")`,
            backgroundPosition: 'center center',
            backgroundRepeat: 'no-repeat',
            zIndex: 100,
            backgroundSize: 'cover',
            position: 'absolute',
            top: 0,
            right: 0,
            bottom: 0,
            left: 0,
            opacity: 0.3,
        }
    },
    content: {
        zIndex: 1000,
        flexGrow: 1
    }
}))

export const DisplayItem = props => {
    const {
        children,
        imageUrl
    } = props;

    const classes = getStyles(imageUrl)();

    return (
        <div className={classes.image}>
            <div className={classes.content}>
                {children}
            </div>
        </div>
    );
}
