import React from 'react';

import {
    Grid,
    Typography,
    Button,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import Paragraph from '../../GenericCompoents/Paragraph';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    }
}));

export const SabatomiRap = props => {
    const { redirectTo } = props;

    const classes = useStyles();

    return (
        <>
            <Typography variant="h3" color="secondary" className={classes.title}>SabatomiRap</Typography>
            <Paragraph
                title=""
                text={[
                    "For over a decade, the open source enthusiasts and makers at the Labitat hackerspace in Copenhagen, Denmark, SoMakeIt in Southampton, UK and other similar groups around the world have worked on MetalicaRap, a project to develop a metal 3D printer and scanning electron microscope combination device at a fraction of the cost of either such device on the open market. A series of unfortunate events set this project back from completion and caused a loss of interest among some of its members.",
                    "We are picking up where they essentially left off, with our own fork of this open source hardware project, and we're calling it SabatomiRap. Like Metallica, Sabaton is a metal band. The device handles subatomic particles (electrons) and it's a rapid replicator like the RepRap.",
                    "We have a new updated bill of materials and improvements to the designs are coming up.",
                    "We thoroughly intend to both use and make available the schematics and kits of this awesome device."
                ]}
            />
            <Grid
                item
                xs={12}
            >
                <Divider className={classes.divider} />
            </Grid>
            <Grid
                item
                container
                xs={12}
                justify="center"
            >
                <Button variant="outlined" onClick={() => redirectTo('project')}>Back to projects</Button>
            </Grid>
        </>
    );
}
