import React, { Button, CardActions } from '@material-ui/core';

import {
    Card, 
    CardContent,
    CardMedia,
    Typography,
    Grid, 
    makeStyles, 
    CardActionArea, 
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    cardHolder: {
        padding: '4px'
    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start'
    },
    cardContentContainer: {
        flexGrow: 1,
        justifyContent: 'space-between'
    },
    cardMedia: {
        paddingTop: '56.25%', 
        backgroundColor: "white",
        height: '100%'
    },
    cardContent: {
        flexGrow: 1,
    },
    cardAction: {
        padding: '0', 
    },
    readButton: {
        borderRadius: `0px 0px ${theme.shape.borderRadius}px ${theme.shape.borderRadius}px`
    },
}));

const CardLink = props => {
    const classes = useStyles();
    const { 
        largeItem, 
        onClick, 
        title, 
        imgSrc,
        preview,
        openText,
        disabled,
        icon
    } = props;
    const clicked = () => onClick ? onClick() : null;

    return (
        <Grid 
            item 
            xs={12} 
            sm={6} 
            md={largeItem ? 8 : 4} 
            lg={largeItem ? 6 : 3} 
            xl={largeItem ? 4 : 2}
            className={classes.cardHolder}
        >
            <Card className={classes.card}>
                <Grid
                    container
                    direction="column"
                    className={classes.cardContentContainer}
                >
                    <Grid
                        item
                        container
                        style={{flexGrow: 1}}
                    >
                        <Grid item xs={8}>
                            <CardContent className={classes.cardContent}>
                                <Typography 
                                    gutterBottom 
                                    variant="h5" 
                                    component="h2"
                                >
                                    {title}
                                </Typography>
                                <Typography 
                                    variant="body2" 
                                    color="textSecondary" 
                                    component="p"
                                >
                                    {preview}
                                </Typography>
                            </CardContent>
                        </Grid>
                        <Grid item xs={4}>
                            <CardActionArea
                                onClick={clicked}
                                disabled={disabled}
                                style={{height: '100%'}}
                            >
                                <CardMedia 
                                    className={classes.cardMedia}
                                    image={imgSrc}
                                    title={title}
                                />
                            </CardActionArea>
                        </Grid>
                    </Grid>
                    <Grid
                        item
                    >
                        <CardActions className={classes.cardAction}>
                            <Button 
                                fullWidth 
                                variant="contained" 
                                color="primary"
                                onClick={clicked}
                                className={classes.readButton}
                                disabled={disabled}
                                endIcon={icon}
                            >
                                {openText ? openText : `View`}
                            </Button>
                        </CardActions>
                    </Grid>
                </Grid>
            </Card>
        </Grid>
    );
}

export default CardLink;
