import React from 'react';

import {
    Grid,
    Typography,
    Divider,
    Link
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import { useHistory } from 'react-router';

import Paragraph from '../../GenericCompoents/Paragraph';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    },
    image: {
        width: "100%"
    },
    caption: {
        textAlign: 'center',
        [theme.breakpoints.down('sm')]: {
            fontSize: '0.55rem'
        }
    },
    link: {
        textAlign: 'center',
        marginBottom: theme.spacing(1)
    },
    question: {

    },
    answer: {
        paddingLeft: theme.spacing(3)
    }
}));

export const Investors = props => {
    const classes = useStyles();
    const history = useHistory();

    return (
        <>
            <Typography color="secondary" className={classes.title} variant="h3">Our Sponsors</Typography>
            <Grid xs={12}><Divider className={classes.divider} /></Grid>
            <img 
                className={classes.image}
                src="images/PageImages/investors-map.png"
                alt="Our sponsors map"
            />
            <Grid
                item
                container
                alignContent="center"
                direction="column"
            >
                <Typography className={classes.caption} variant="caption">VULPINE DESIGNS LTD. 8 UNION SQ, PH13 9AE, COUPAR ANGUS, SCOTLAND, UK</Typography>
                <Typography className={classes.caption} variant="caption">VULPINE DESIGNS LTD. 6 SULLIVAN'S QUAY, CORK CITY, IRELAND, EU</Typography>
                <Typography className={classes.caption} variant="caption">VULPINE LABS INC. 1903 CYNTHIA DRIVE, CHAMPAIGN, ILLINOIS, USA 61821</Typography>
            </Grid>
            <br />
            <Paragraph
                title=""
                text={[
                    "We at Vulpine Designs and Labs are the world's leading experts on the application of genetics to morphological self-actualisation. Our moats are our pace and approach to innovation in the gene editing space. We treat genetic editing not as a fractal of endless detail, but as software to improve. We go back to first principles and design to build a whole system solution, as opposed to the approach of many scientists (seemingly to make the smallest possible product to attain an academic qualification or publication). We are solutions driven and we regard 'impossible' as illogical.",
                    "We also derive great strength from our partnerships with fellow startups, keeping risk low and supporting one another.",
                ]}
            />
            <Grid
                item 
                container
                xs={12}
                justify="center"
            >
                <Grid 
                    item 
                    xs={12}
                    className={classes.link}
                >
                    <Typography variant="subtitle1">See our investors fact sheet <Link href="/investor-fact-sheet.pdf">here</Link></Typography>
                </Grid>
            </Grid>
            <br />
            <Paragraph
                title="Investor FAQs"
                text={[
                    <div className={classes.question}>Q: How long should I wait for a Return On Investment?</div>,
                    <div className={classes.answer}>A: A sensible timeframe for large investments in this company would be 10-15 years. Shorter timeframes are possible for smaller  investments.</div>,
                    <div className={classes.question}>Q: Can I invest time instead of or as well as money?</div>,
                    <div className={classes.answer}>A: Absolutely, yes. Let's discuss how, please contact us. We may have jobs available here.</div>,
                    <div className={classes.question}>Q: When will you go public?</div>,
                    <div className={classes.answer}>A: This is a privately held B (Benefit) Corporation. We will remain privately held, permanently.</div>,
                    <div className={classes.question}>Q: So why should I invest, then?</div>,
                    <div className={classes.answer}>A: We are going to create a profit which is not only, but does not exclude, money. An evolution revolution has benefits for everyone including you, and not only in your wallet but also your health and longevity.</div>,
                    <div className={classes.question}>Q: I would be interested but I think you need to rewrite your business plan again. Will you do that just for me?</div>,
                    <div className={classes.answer}>A: We spend our time primarily focused on developing our gene editing systems and related projects. Should the benefits of these  systems need a more clear explanation in our business plan, we would appreciate your suggestions and assistance in creating an  improved plan.</div>,
                    <div className={classes.question}>Q: I don't have any cash or time myself but I know someone who might be interested.</div>,
                    <div className={classes.answer}>A: Please provide them with a link to our website! Our main mode of growth is organic, through word of mouth.</div>,
                    <div className={classes.question}>Q: Can I join your board of directors?</div>,
                    <div className={classes.answer}>A: You need to be very serious and committed about the long term success of our goals and about freedom of form to be on our board. It is not something for the faint of heart. If you are very serious and committed and want to be a part of running this company, do not necessarily expect success in your application, but by all means try applying by contacting us here.</div>,
                    <div className={classes.question}>Q: Can I buy you out?</div>,
                    <div className={classes.answer}>A: No. We can, however, negotiate sensible minority percentages of our private company shares.</div>,
                    <div className={classes.question}>Q: What will Vulpine Designs do with my hard earned cash?</div>,
                    <div className={classes.answer}>A: After we have carried out due diligence, we'll discuss with you a fair and sensible way to feed your investment into advancement of our goals and shortening our timeframes for achieving the previously impossible. With enough money and enough time, we could solve many problems for the world. We have many ideas beyond what you see on this website. We always have more vision than time. Please contact us. Rest assured, we choose our expenditure wisely.</div>,
                    <div className={classes.question}>Q: Wait a second, I think I saw a Vulpine Designs Patreon page! Are you guys not serious, and just making some sort of roleplay or  media meta-content?</div>,
                    <div className={classes.answer}>A: We use Patreon because it allows for recurring monthly donations from the crowd, as in recursive crowdfunding. It provides a stable and steady pre-profit income stream from those who do not possess large sums of money but who want to support our work. We will establish sufficient income streams to render Patreon unnecessary as we grow, but for now Patreon is a chicken-and-egg-scenario relief mechanism and covers some of our basic running costs for IT. Even when we are profitable, some people may wish to continue contributing in this fashion. We will check with them. Our work is genuinely for freedom of form, for real life purposes, and is NOT any form of roleplay or media meta-content. If you have a genuine reason to believe that the presence of a Patreon income will clash with your ability to invest in us, please explain it fully in private to us <Link onClick={() => history.push('/contact')}>here</Link>.</div>,
                    <div className={classes.question}>Q: I run a country's military. I want supersoldiers. Here's a blank cheque. Will you take it?</div>,
                    <div className={classes.answer}>A: No. Please read our summary on Freedom of Form.</div>,
                    <div className={classes.question}>Q: Can I buy the patent rights or licensing of your intellectual property?</div>,
                    <div className={classes.answer}>A: No. Please see the GNU General Public License Version 2 <Link href="https://opensource.org/licenses/gpl-2.0.php">here</Link>, and the Open Hardware License we use <Link href="https://tapr.org/the-tapr-open-hardware-license">here</Link>.</div>,
                ]}
            />
        </>
    );
};
