import React, { Button, CardActions } from '@material-ui/core';

import {
    Card, 
    CardContent,
    CardMedia,
    Typography,
    Grid, 
    makeStyles, 
    CardActionArea, 
} from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    cardHolder: {
        padding: '4px'
    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'flex-start'
    },
    cardMedia: {
        paddingTop: '56.25%', 
        backgroundColor: "white"
    },
    cardContent: {
        flexGrow: 1,
    },
    cardAction: {
        padding: '0', 
    },
    readButton: {
        borderRadius: `0px 0px ${theme.shape.borderRadius}px ${theme.shape.borderRadius}px`
    },
}));

const CardLink = props => {
    const classes = useStyles();
    const {
        onClick, 
        title, 
        imgSrc,
        preview,
        openText,
        disabled
    } = props;
    const clicked = () => onClick ? onClick() : null;

    return (
        <Grid 
            item 
            xs={12} 
            sm={6}
            className={classes.cardHolder}
        >
            <Card className={classes.card}>
                <CardActionArea
                    onClick={clicked}
                    disabled={disabled}
                >
                    <CardMedia 
                        className={classes.cardMedia}
                        image={imgSrc}
                        title={title}
                    />
                </CardActionArea>
                <CardContent className={classes.cardContent}>
                    <Typography 
                        gutterBottom 
                        variant="h5" 
                        component="h2"
                    >
                        {title}
                    </Typography>
                    <Typography 
                        variant="body2" 
                        color="textSecondary" 
                        component="p"
                    >
                        {preview}
                    </Typography>
                </CardContent>
                <CardActions className={classes.cardAction}>
                    <Button 
                        fullWidth 
                        variant="contained" 
                        color="primary"
                        onClick={clicked}
                        className={classes.readButton}
                        disabled={disabled}
                    >
                        {openText ? openText : `View`}
                    </Button>
                </CardActions>
            </Card>
        </Grid>
    );
}

export default CardLink;
