import React from 'react';

import {
    makeStyles, 
    AppBar,
    Toolbar,
    Hidden,
    ButtonBase, 
    Grid,
    Typography, 
} from '@material-ui/core';

import MenuIcon from '@material-ui/icons/Menu';

const useStyles = makeStyles(theme => ({
    footer: {
        top: 'auto',
        bottom: 0,
        alignItems: 'center',
    }, 
    selectArea: {
        heigh: "100%",
        width: "100%"
    }, 
    disclaimer: {
        top: 'auto',
        bottom: 0,
        alignItems: 'center',
        backgroundColor: theme.palette.background.paper,
        color: theme.palette.text.primary
    },
    disclaimerText: {
        flexDirection: 'column',
        alignItems: 'flex-start',
        paddingTop: theme.spacing(1)
    }
}));

const Footer = props => {
    const classes = useStyles();

    return (
        <>
            <Hidden mdUp>
                <AppBar position="sticky" className={classes.footer}>
                    <ButtonBase 
                        className={classes.selectArea} 
                        onClick={() => props.toggleDraw('bottom')}
                    >
                        <Toolbar>
                            <Grid container>
                                <Grid xs={12} item></Grid>
                                <Grid xs={12} item><MenuIcon /></Grid>
                                <Grid xs={12} item></Grid>
                            </Grid>
                        </Toolbar>
                    </ButtonBase>
                </AppBar>
            </Hidden>
            <Hidden smDown>
                <AppBar position="static" className={classes.disclaimer}>
                    <Toolbar className={classes.disclaimerText}>
                        <Typography variant="body2">This website's source code and stylesheets are available for re-use. Just please use different content and use it for something other than a biotech company.</Typography>
                        <Typography variant="body2">Made in 2021 in France, the UK, Australia, the USA and Spain by Vulpine Designs Ltd, Cork, Ireland; Vulpine Designs Ltd, Coupar Angus, UK; and Vulpine Labs Inc, Champaign, Illinois, USA.</Typography>
                    </Toolbar>
                </AppBar>
            </Hidden>
        </>
    );
}

export default Footer;
