import React, { useState, useRef } from 'react'; 

import { 
    Grid, 
    Typography, 
    makeStyles,
    Button,
    Link
} from '@material-ui/core'; 

import YouTube from 'react-youtube';

import Paragraphs from '../../GenericCompoents/Paragraph';

import HeadlineDisplay from './HeadlineDisplay';

import {
    dev
} from '../../helper';

const useStyles = makeStyles(theme => ({
    content: {
        padding: 0
    },
    sectionTitle: {
        textAlign: 'left'
    },
    button: {
        margin: 4
    },
    youtubePlayer: {
        height: '100%',
        width: '100%',
    },
    youtubePlayerContainer: {
        height: 360,
        width: 640,
        [theme.breakpoints.down('sm')]: {
            height: '56.3vw',
            width: '100vw'
        }
    },
    linkText: {
        textAlign: 'center'
    }
}));

const getFrontPageData = async stateSetter => {
    const pageData = await fetch(`${dev ? '/Data' : '/api'}/FrontPage${dev ? '.json' : ''}`)
        .then(res => res.json())
        .then(data => data.list)
        .catch(e => {
            console.log(`Error getting front page data: ${e}`);
            return [];
        });
    stateSetter({
        hasLoaded: true,
        pageData: pageData
    });
};

const Headline = props => {
    const { redirectTo } = props;

    const classes = useStyles();
    const [pageState, setPageState] = useState({
        hasLoaded: false,
        pageData: []
    });
    if(!pageState.hasLoaded)
        getFrontPageData(setPageState);

    const introRef = useRef(null);
    const bCorp = useRef(null);
    const writeYourselfRef = useRef(null);
    const historyRef = useRef(null);
    const objectivesRef = useRef(null);
    const headlineDisplayData = [
        {
            bgImage: "/images/Abstract/who-we-are.jfif",
            title: "Who we are.",
            desc: "Freedom of Form is the concept of being able to choose for oneself, one's physical and biological manifestation in this world, by way of transforming from what one is at present to what one wishes to become.",
            link: "nottinghack-cpu",
            id: 'intro',
            onClick: () => introRef.current.scrollIntoView({behavior: "smooth", block: "center"}),
        },
        {
            bgImage: "/images/Abstract/dna-strand.jpg",
            title: "Write yourself. Open your source code.",
            desc: "Design your body to be resilient and to express your inner self. Use the avatar editor of real life to design transdifferentiative processes of transformation and redefine evolution itself!",
            link: "nottinghack-cpu",
            id: 'intro',
            onClick: () => writeYourselfRef.current.scrollIntoView({behavior: "smooth", block: "center"}),
        },
        {
            bgImage: "/images/Abstract/burjKhalifa.webp",
            title: "Come speak to us.",
            desc: "We have active contributers and volunteers globally, and have and will attend a number of conferences also globally, see where you can speak to us in person!",
            link: "nottinghack-cpu",
            id: 'events',
            onClick: () => redirectTo('events'),
        }
    ];

    return (
        <>
            <Grid
                item
                xs={null}
                lg={1}
            />
            <Grid
                item
                container
                xs={12}
                lg={10}
            >
                <HeadlineDisplay 
                    redirectTo={redirectTo}
                    headlineDisplayData={headlineDisplayData}
                />
            </Grid>
            <Grid
                item
                xs={null}
                lg={1}
            />
            <Grid
                item
                xs={null}
                lg={2}
            />
            <Grid
                item
                container
                xs={12}
                lg={8}
            >
                <Grid 
                    container 
                    item 
                    xs={12} 
                    spacing={1} 
                    justify="center" 
                    alignItems="stretch" 
                >
                    <Paragraphs
                        id="intro"
                        ref={introRef}
                        title="Introduction"
                        text={[
                            "Freedom of Form is the concept of being able to choose for oneself, one's physical and biological manifestation in this world, by way of transforming from what one is at present to what one wishes to become. This includes morphological freedom, but it also includes the means to freedom from diseases and other aberrations which may negatively impact one's enjoyment of oneself. It also includes, most importantly, a freedom to choose one's own morphological and biological destiny. It is not as far-fetched a concept as it might seem either; in many VR worlds and video games, the idea of an avatar which you can modify is practically essential. All we need is a real world avatar editor.", 
                            <>    
                                <Grid 
                                    item
                                    container
                                    xs={12} 
                                    justify="center"
                                >
                                    <YouTube 
                                        containerClassName={classes.youtubePlayerContainer} 
                                        className={classes.youtubePlayer}
                                        videoId="z6P3r742ObA"
                                    />
                                </Grid>
                            </>,
                            "Vulpine Designs Ltd is our software development and content creation side. Based formally in our Perthshire, UK and Cork, Ireland offices, our Pan-Eurasian team is behind the GUESS and all its modules, and contributes to the schematics of our hardware.", 
                            "Vulpine Labs Inc. is for the establishment of our laboratory for hardware and wetware development. Based formally in the United States, our Pan-American team also handles some 3D modelling systems and VR development.", 
                            "Our IT infrastructure is hosted worldwide."
                        ]}
                    > 
                        <Grid
                            container
                            justify="center"
                            spacing={12}
                        >
                            <Grid 
                                item
                                container
                                xs={12}
                                justify="center"
                                style={{ height: 15 }}
                            />
                            <Grid 
                                item 
                                container
                                xs={12} 
                                justify="center"
                            >
                                <Button 
                                    className={classes.button} 
                                    color="primary" 
                                    variant="outlined"
                                    onClick={() => redirectTo('project/guess')}
                                >
                                    Guess
                                </Button>
                                <Button 
                                    className={classes.button} 
                                    color="primary" 
                                    variant="outlined"
                                    onClick={() => redirectTo('project/laberation')}
                                >
                                    Laberation
                                </Button>
                                <Button 
                                    className={classes.button} 
                                    color="primary" 
                                    variant="outlined"
                                    onClick={() => redirectTo('project/sabatomirap')}
                                >
                                    Sabatomirap
                                </Button>
                            </Grid>
                            <Grid 
                                item 
                                container 
                                xs={12} 
                                justify="center"
                            >
                            </Grid>
                        </Grid>
                    </Paragraphs>
                    <Paragraphs
                        id="bCorp"
                        ref={bCorp}
                        title="B Corporation"
                        text={[
                            "At Vulpine Labs, we put Corporate Responsibility front and centre. We are a Public Benefit Corporation and incorporated as such in the US state of Illinois. This means that rather than restricting ourselves to benefitting our bank balances and shareholders, we intend to equally benefit the environment, our customers, our suppliers, our own staff and society as a whole. This is possible through remaining privately held and committed to open-source business, development and licensing models. We thoroughly believe that this stance will be absolutely necessary to our success and to our ability to disrupt multiple industries through innovation.", 
                            "Our work for our community takes place at the local, enthusiast, industry and global levels. Check it out here: Community page."
                        ]}
                    >
                        <Grid
                            container
                            justify="center"
                            spacing={12}
                        >
                            <Button 
                                className={classes.button} 
                                color="primary" 
                                variant="outlined"
                                onClick={() => redirectTo('investors')}
                            >
                                Investors
                            </Button>
                        </Grid>
                    </Paragraphs>
                    <Paragraphs
                        id="writeYourself"
                        ref={writeYourselfRef}
                        title="Write yourself. Open your source code. Free your form. Share the way."
                        text={[
                            "Design your body to be resilient and to express your inner self. Use the avatar editor of real life to design transdifferentiative processes of transformation and redefine evolution itself!",
                            "Want to see the end of genetic diseases, not just through repeatedly treating them, but actual cures? Want to be an anime-style catgirl in the real world? Want to adapt to space travel and changing climates? Want to work out how to make yourself more genetically immune to exotic poisons and radiation? These are but a few examples of the ultimate form of self-actualisation we can create together.",
                            "Our tools are designed to make that future more possible, and our products and services, to help you make the most of our tools to make that future happen!"
                        ]}
                    />
                    <Paragraphs
                        id="history"
                        ref={historyRef}
                        title="History"
                        text={[
                            "The brainchild of our CEO and founder Daniel Davies, the GUESS and Geneticiser system concept came about when he was writing a science fiction book in 2008 about causing alterations to and curing diseases in the adult human body using a genetic editing system, only to realise the more he wrote it that it would make actual scientific sense and be plausible to build such a system.",
                            "He dedicated his life to developing such a system, morally solidified with the principle of freedom of form: an evolution revolution, in which people now could dictate their own evolutionary paths and within their lifetimes, become whatever they want to physically and biologically be.",
                            "He then proceeded to take a job at a call centre to earn the money to pay for community college, to have the minimum qualifications to enter the University of Southampton to get a degree in Biochemistry so he would be better able to develop the system based on up to date science. Using principles from Lego model design, from software development and from multi-stage spacefaring rockets as a base, he founded Vulpine Designs Unlimited in 2009 as a Sole Trader business, followed by incorporating in Ireland in 2016, the UK in 2017 and the USA in 2020.",
                            "The SabatomiRap project is a fork of the MetalicaRap project, which was from a group largely based at Labitat hackerspace, Copenhagen, Denmark. We intend to finish what their team began to create, and to make available all the relevant information on it once we have a proven working device, and on how to obtain or build one."
                        ]}
                    />
                    <Paragraphs
                        id="objectives"
                        ref={objectivesRef}
                        title="Objectives"
                        text={[]}
                    >
                        <ul>
                            <li><Typography variant="subtitle1">To ensure successful development of genetic editing and transformation technologies for freedom of form (including health and morphological freedom).</Typography></li>
                            <li><Typography variant="subtitle1">To ensure there is equal and permanent availability of these technologies to as many people as possible.</Typography></li>
                            <li><Typography variant="subtitle1">To support and nurture a community of developers and users who participate and also help one another.</Typography></li>
                            <li><Typography variant="subtitle1">To assist the effort to make intelligent life multiplanetary in ways that enhance and are enhanced by the success of our other objectives.</Typography></li>
                        </ul>
                        <Typography variant="h5">Strategy</Typography>
                        <ul>
                            <li><Typography variant="subtitle1">Develop the tools to edit any and all of our genes.</Typography></li>
                            <li><Typography variant="subtitle1">Ensure they are available to the world at a sensible price.</Typography></li>
                            <li><Typography variant="subtitle1">Develop a global network for sharing and developing gene edits and vectors.</Typography></li>
                            <li><Typography variant="subtitle1">Make using our tools simple and safe.</Typography></li>
                            <li><Typography variant="subtitle1">Grow a new paradigm, and be the fount of knowledge and innovation in the field.</Typography></li>
                            <li><Typography variant="subtitle1">Achieve our goals by doing the right thing and using the highest ethical standards.</Typography></li>
                        </ul>
                        <Typography variant="h5">First it is important to recognise that deadlines in this context cannot be absolute due to the quantity of unknowns.</Typography>
                        <ol>
                            <li><Typography variant="subtitle1">Release early versions of INITIATOR SET, the realtime compiler of genetic information. This software will be very helpful to laboratories and researchers around the world, vastly cutting the time and effort it takes to understand which proteins are created from which genes in which circumstances. Offer support, training and co-development for this. Timeframe: this can be achieved within a few months</Typography></li>
                            <li><Typography variant="subtitle1">Create additional modules of the GUESS to offer as standalones and in conjunction with one another. Improve support, training and co-development. Timeframe: this can be achieved within 2-3 years, or sooner with suitable funding.</Typography></li>
                            <li><Typography variant="subtitle1">Build out the SabatomiRap project and make it available as a kit and open source. Use this to equip our lab. Timeframe: about 3 years.</Typography></li>
                            <li><Typography variant="subtitle1">Create prototypes of the Geneticiser device and experiment with it. This objective would be highly facilitated by having a lab. Timeframe: without a lab: 10+ years. With a lab: 4 years.</Typography></li>
                            <li><Typography variant="subtitle1">Complete the GUESS to version 1.0, capable of simulating the whole body and the development of gene editing vectors for it, and of outputting the created vectors to be printed by a Geneticiser or similar. Timeframe: 10-20 years if unfunded. With funds, 4-5 years.</Typography></li>
                            <li><Typography variant="subtitle1">Create and promote the Laberation network and use it to begin generating new revenue from the content created in the GUESS. Without funds: 8 years or so. With funds: 4 years.</Typography></li>
                        </ol>
                        
                    </Paragraphs>
                    
                    <Grid 
                        item
                        container
                        xs={12} 
                        justify="center"
                    >
                        <Link 
                            onClick={() => redirectTo('events')}
                            className={classes.linkText}
                        >
                            Come see where you can meet us in person, we host and attend a number of events globally.
                        </Link>
                    </Grid>
                </Grid>
            </Grid>
            <Grid
                item
                xs={null}
                lg={2}
            />
        </>
    );
}

export default Headline;
