import React from 'react';

import { Grid } from '@material-ui/core';
import {
    makeStyles
} from '@material-ui/core/styles';

import Headlines from './Headlines/Headlines'; 
import Error from './Error';
import Projects from './Projects';
import { Contact } from './Contact/Contact';
import { Plans } from './Plans/Plans';
import { Product } from './Products/Products';
import { Investors } from './Investors/Investors';
import { Community } from './Community/Community';
import { Team } from './Team/Team';
import { Events } from './Events/Events';

const useStyles = makeStyles(theme => ({
    content: {
        height: '100%'
    },
}))

export const Pages = props => {
    const classes = useStyles()
    const { 
        page, 
        redirectTo, 
        postId 
    } = props;

    // A dict of page components with their respective URI name 
    const pagesDict = {
        "home": (<Headlines redirectTo={redirectTo} />),
        "project": (<Projects postId={postId} redirectTo={redirectTo} />),
        "contact": (<Contact />),
        "plans": (<Plans />),
        "products": (<Product />),
        "investors": (<Investors />),
        "community": (<Community />),
        "team": (<Team />),
        "events": (<Events />)
    };

    const largePageDict = {
        "home": true
    };

    const largePage = !!largePageDict[page];

    // Get the selected page component, else show an error 
    let pageProp = pagesDict[page] ? 
        pagesDict[page] : 
        (
            <Error />
        );

    return (
        <Grid 
            container 
            className={classes.content}
            justify="center"
        >
            <Grid
                item
                xs={null}
                lg={largePage ? 0 : 2}
            />
            <Grid
                item
                container
                justify="center"
                xs={12}
                lg={largePage ? 12 : 8}
            >
                { pageProp }
            </Grid>
            <Grid
                item
                xs={null}
                lg={largePage ? 0 : 2}
            />
        </Grid>
    );
}

export default Pages;
