import React from 'react';

import {
    Grid,
    Typography, 
    CardActionArea
} from '@material-ui/core';
import {
    makeStyles
} from '@material-ui/core/styles';

import Carousel from 'react-material-ui-carousel'; 

import { DisplayItem } from '../../GenericCompoents/DisplayItem';

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: '2.7em'
    },
    body: {
        fontSize: '1.4em'
    },
    select: {
        paddingTop: theme.spacing(2),
        paddingBottom: theme.spacing(4),
        [theme.breakpoints.down('xs')]: {
            minHeight: '46vh',
            paddingTop: 0,
            paddingBottom: 0,
        },
        minHeight: '40vh'
    },
    carouselRoot: {
        flexGrow: 1
    }
}));

const HeadlineDisplay = props => {
    const { 
        headlineDisplayData
    } = props;
    const classes = useStyles();

    const items = headlineDisplayData.map((item, index) => 
        <div 
            key={`${item.bgImage}-${index}`}
        >
            <DisplayItem imageUrl={item.bgImage}>
                <CardActionArea
                    className={classes.select}
                    onClick={item.onClick}
                >
                    <Grid container>
                        <Grid item lg={1} xs={false} />
                        <Grid item lg={10} xs={12}>
                            <Typography className={classes.title} variant="h2">
                                {item.title}
                            </Typography>
                            <Typography className={classes.body} variant="h5">
                                {item.desc}
                            </Typography>
                            <Typography className={classes.body} variant="h5" color="secondary">
                                Learn More
                            </Typography>
                        </Grid>
                        <Grid item lg={1} xs={false} />
                    </Grid>
                </CardActionArea>
            </DisplayItem>
        </div>
    );
    
    return (
        <Carousel 
            animation="slide"
            className={classes.carouselRoot}
        >
            {items}
        </Carousel>
    );
}

export default HeadlineDisplay;
