import React, { useState } from 'react';

import {
    Grid,
    Typography, 
    Divider,
    Dialog,
    DialogTitle
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import Paragraph from '../../GenericCompoents/Paragraph';
import ProfileLink from '../../GenericCompoents/ProfileLink';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    },
    subTitle: {
        textAlign: "center"
    },
    dialogText: {
        padding: '0px 24px 16px 24px'
    }
}));

export const Team = props => {
    const [dialogOptions, setDialogOptions] = useState({ open: false, text: '', title: '' });

    const classes = useStyles();

    return (
        <>
            <Dialog 
                open={dialogOptions.open}
                onClose={() => setDialogOptions({ open: false, text: '', title: '' })}
                scroll="body"
            >
                <DialogTitle>{dialogOptions.title}</DialogTitle>
                <Typography 
                    component="div" 
                    variant="paragraph1"
                    className={classes.dialogText}
                >
                    {dialogOptions.text}
                </Typography>
            </Dialog>
            <Typography className={classes.title} color="secondary" variant="h3">Our Team</Typography>
            <Grid item container xs={12} justify="center">
                <Typography className={classes.subTitle} variant="h5">Drawn Together To Build A Better Future</Typography>
            </Grid>
            <Grid 
                item
                xs={12}
                container
                justify="flex-start"
            >
                <Grid item xs={12} className={classes.divider}><Divider /></Grid>
                <Grid 
                    item 
                    container 
                    xs={12} 
                    className={classes.divider}
                    justify="center"
                >
                    <ProfileLink
                        title="Dan Davies"
                        imgSrc="/images/ProfileIcons/danfox-davies-icon.png"
                        text="CEO and Founder, Genetic Biodesign Systems Engineer."
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `I've built a team from scratch. I handle relationships with other startups, clients, customers, investors and users. I've 12 years' entrepreneurial experience. I majored in Biochemistry with Genetics & Bioinformatics included and I'm from an IT & 3D modelling background. Every setback is my opportunity to learn, every leap forward is measured and appreciated. I intercept trends of the future in technology, society and monetisation potential.`, 
                                title: 'Dan Davies'
                            })
                        }
                    />
                    {/* <ProfileLink
                        title="Alynna Kelly"
                        imgSrc="/images/ProfileIcons/alynna-icon.png"
                        text="CTO, IT Systems and AI."
                    /> */}
                    <ProfileLink
                        title="Will Towler, PhD"
                        imgSrc="/images/ProfileIcons/will-icon.jpg"
                        text="Evolutionary and Developmental Biologist"
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `Dr. Towler is a developmental biologist with his Ph.D. in cell and molecular biology from the University of Pennsylvania. His work on the rare genetic bone disease fibrodysplasia ossificans progressiva greatly improved our understanding of the skeletal phenotype of the disease and the underlying shifts in embryonic signaling pathways it causes. Technical specialties are in histology, microscopy, mouse genetics, and molecular analysis of tissues.`, 
                                title: 'Will Towler'
                            })
                        }
                    />
                    <ProfileLink
                        title="Tracy Goode"
                        imgSrc="/images/ProfileIcons/tracy-icon.jpg"
                        text="Admin & Personnel Manager"
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `With decades of office & legal administration and startup experience, Tracy brings a positive and steady approach to our paperwork needs. She can keep a business together through thick and thin, and file paperwork when it's needed no matter what. Eloquent and thoughtful, determined and helpful. She also has plenty of experience in logistical planning and management, driving, timesheets and most recently, some computer programming.`, 
                                title: 'Tracy Goode'
                            })
                        }
                    />
                    {/* <ProfileLink
                        title="Mircea Filipescu Alexandru"
                        imgSrc="/images/ProfileIcons/anon-icon.jpg"
                        text="European operation director."
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `With decades of office & legal administration and startup experience, Tracy brings a positive and steady approach to our paperwork needs. She can keep a business together through thick and thin, and file paperwork when it's needed no matter what. Eloquent and thoughtful, determined and helpful. She also has plenty of experience in logistical planning and management, driving, timesheets and most recently, some computer programming.`, 
                                title: 'Will Towler'
                            })
                        }
                    /> */}
                    <ProfileLink
                        title="Jordan Eckhoff"
                        imgSrc="/images/ProfileIcons/anon-icon.jpg"
                        text="Systems Biologist."
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `Jordan Eckhoff is a smart mind and highly capable of handling the big picture of biological systems and networks. If not working on biological systems, Jordan is probably working on what needs to be done to make systems that make those systems. Knows Python, Java, MATLAB, wants to learn R. Bachelor of Science in Biomedical Engineering, working on Masters of Engineering in Computational Bioengineering, both from Rutgers Uni, New Jersey, USA.`, 
                                title: 'Jordan Eckhoff'
                            })
                        }
                    />
                    {/* <ProfileLink
                        title="John Middleton "
                        imgSrc="/images/ProfileIcons/anon-icon.jpg"
                        text="Accounts and Finances."
                    /> */}
                    <ProfileLink
                        title="Lucas Barros"
                        imgSrc="/images/ProfileIcons/lucas-icon.png"
                        text="3D Hardware Designer"
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `Lucas brings a good eye for 3D modelling, spatial awareness and visual programming. He's great with conceptualising our gene editing systems hardware and logos for our systems. His background is in working with wood and metals, IT and games design. He grasps new visual concepts and gives them style and practicality. To go from patent drawings to product prototype, we ask Lucas to work on it.`, 
                                title: 'Lucas Barros'
                            })
                        }
                    />
                    <ProfileLink
                        title="Nicholas Panos"
                        imgSrc="/images/ProfileIcons/nicholas-icon.jpg"
                        text="Software Development Manager and Systems Design Consultant"
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `Nicholas Panos is strongly connected to software design principles, programming and all manner of useful knowledge for making those computers compute. With a strong aptitude for IT problem solving and systems structural reliability, Nicholas brings real drive and aspects of project management to the team. Knows Java, Lua, Python. Major: IT (Software Development) Southern Alberta Institute of Technology, Calgary, Alberta, Canada.`, 
                                title: 'Nicholas Panos'
                            })
                        }
                    />
                    <ProfileLink
                        title="Jason Alexander"
                        imgSrc="/images/ProfileIcons/jason-icon.jpg"
                        text="Computer Scientist & Consultant"
                        onClick={() => 
                            setDialogOptions({ 
                                open: true, 
                                text: `A consultant with a varied background working in both software engineering on the web and embedded devices, on custom CPUs and hardware as well as theorhetical computer sciences, Jason works with a variety of all of Vulpine Designs software and hardware systems. Previously working on systems for the Waymo (formerly the Google Self Driving Car Project), the UKs ambulance services and even the UK's Atomic Weapons Establishment.`, 
                                title: 'Jason Alexander'
                            })
                        }
                    />
                </Grid>
                <Paragraph
                    title="Role Vacancies"
                    text={[
                        "We have VACANCIES for volunteers with a view to potentially becoming paid staff as finances improve. We are looking for programmers who know C, Java, Python, R, Javascript, Erlang, DNA, RNA, Amino Acids, FASTA, Blockchain and/or advanced encryption.",
                        "We are also looking for a secretary and a sales manager. A basic knowledge or interest in biochemistry and genetics will serve you well, and you will be encouraged to read and learn more on the job.",
                    ]}
                >
                    <br />
                    <Typography variant="subtitle1">Please see the contact us page to get in touch.</Typography>
                </Paragraph>
                <Paragraph
                    title="Disclaimers"
                    text={[
                        "Diversity: the CEO wishes to express awareness with regard to the current diversity-balance of the staff, and to state that this is unintentional, and is simply based on who was interested so far in working with us. If a more diversity-balanced pool of individuals were available to pick from, a more balanced team would result. No bias is intended. This means, if you want to volunteer for or be employed by us but come from a different background or ethnicity or gender or whatever, than the rest of us so far, you shall be treated no differently than if we were all of the same ethnicity, gender etcetera as yourself, and we will ensure this is upheld to the best of our ability.",    
                        "Photographs: We include photographs only by permission of each member of staff listed. This is to ensure privacy is an option for our colleagues."
                    ]}
                />
            </Grid>
        </>
    );
};
