import React from 'react';

import {
    Typography,
    Grid,
    Divider
} from '@material-ui/core';

import {
    makeStyles
} from '@material-ui/core/styles';

import YouTube from 'react-youtube';

const useStyles = makeStyles(theme => ({
    divider: {
        marginTop: 5,
        marginBottom: 5,
    },
    youtubePlayer: {
        height: '100%',
        width: '100%',
    },
    youtubePlayerContainer: {
        height: 360,
        width: 640,
        [theme.breakpoints.down('sm')]: {
            height: '56.3vw',
            width: '100vw'
        }
    }
}));

export const Product = props => {
    const classes = useStyles();

    return (
        <>
            <Typography className={classes.title} color="secondary" variant="h3">Our Products</Typography>
            <Grid item xs={12}><Divider className={classes.divider} /></Grid>
            <Grid 
                item
                xs={12}
            >
                <Typography variant="h5">We currently offer the following</Typography>
                <ul>
                    <li><Typography variant="body1">INITIATOR SET - Standalone module support - monthly or annual contracts</Typography></li>
                    <li><Typography variant="body1">INITIATOR SET - Standalone module training - per session or full course, per n person group</Typography></li>
                    <li><Typography variant="body1">INITIATOR SET - Co-development package - monthly or annual contract to work with you and put your needs for INITIATOR SET front and centre for a dedicated branch of the system for your bespoke applications.</Typography></li>
                </ul>
                <br />
                <Divider className={classes.divider} />
                <Typography variant="h5">We will soon also offer</Typography>
                <ul>
                    <li><Typography variant="body1">Support, training and co-development for other modules</Typography></li>
                    <li><Typography variant="body1">Laberation - Hosting of gene edits and 'Molecures' - Microtransactional cryptocurrency payments</Typography></li>
                    <li><Typography variant="body1">Geneticiser - Kit</Typography></li>
                    <li><Typography variant="body1">SabatomiRap - Kit</Typography></li>
                    <li><Typography variant="body1">Merchendise - Various merch at our Redbubble store.</Typography></li>
                    <li><Typography variant="body1">We set aside a part of the proceeds of every sale to carbon-offset the lifecycle of our products and the energy costs and maintenance of our services.</Typography></li>
                </ul>
                <Divider className={classes.divider} />
                <Grid container>
                    <Grid 
                        item 
                        container
                        xs={12}
                        justify="center"
                    >
                        <Typography variant="h5">A demo of INITIATOR_SET</Typography>
                    </Grid>
                    <Grid 
                        item 
                        container
                        xs={12}
                        justify="center"
                    >
                        <YouTube 
                            containerClassName={classes.youtubePlayerContainer}
                            className={classes.youtubePlayer}
                            videoId="08uuz2B9Jpk"
                        />
                    </Grid>
                </Grid>
            </Grid>
        </>
    );
}
